const GetTronWeb = new Promise(function (resolve) {
    console.log('Connection starting...'/*, 'window.tronWeb=', window.tronWeb, ' window.tronWeb.ready=', window.tronWeb.ready*/)
    const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready
    };    

    if(tronWebState.loggedIn) {
        console.log('tronWebState.installed, returning window.tronWeb, window.tronWeb=', window.tronWeb)
        resolve(window.tronWeb);
    } else {
        console.log('tronWebState not installed, tronWebState.installed = ', tronWebState.installed)

        let tries = 0;

        const timer = setInterval( () => {
            console.log('start timer try=', tries , ' tronWebState.installed=', tronWebState.installed)
            if(tries >= 10) {
                console.log('tronWebState not installed, connecting to node, try =', tries)

                window.tronWeb = _tronWeb

                clearInterval(timer);
                tronWebState.installed = !!window.tronWeb;
                tronWebState.loggedIn = true // window.tronWeb && window.tronWeb.ready;
                console.log('tronWebState.installed = ', tronWebState.installed , ' window.tronWeb=', window.tronWeb, ' window.tronWeb.ready=', window.tronWeb.ready)
                startEventListener(window.tronWeb)
                resolve(window.tronWeb);
            }

            tronWebState.installed = !!window.tronWeb;
            tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

            if(!tronWebState.loggedIn) {
                console.log('Logged in =', tronWebState.loggedIn, 'keep trying try=', tries);
                return tries++;
            } else {
                console.log('stop timer on try=', tries , ' tronWebState.installed=', tronWebState.installed, ' window.tronWeb=', window.tronWeb)
                clearInterval(timer)
                startEventListener(window.tronWeb)
                resolve(window.tronWeb)
            }
        }, 100);
    }
})

const GetBallance = async function () {
    console.log('Get Account then get Ballance starting!')
    let tweb = await store.state.tronWeb.instance
    let addr
    return new Promise((resolve) => {
        addr = tweb.defaultAddress.base58
        if (addr != false) {
            if (typeof addr !== 'undefined'){
                store.dispatch('ActionSetAddress', addr),
                resolve();
            }
        }
    })
    .then( () => {
        console.log('GetBallance starting! Address:', addr, '\n object ', tweb),
        tweb.trx.getBalance(addr, (err, balance) => {
            if(err)
                return console.error('something wrong', err);
            console.log('balance =', balance);
            store.dispatch('ActionSetTRXBal', balance)
        })
    })
}

const blockToTime = function (blocks) {
    let hrs = blocks / blockInHour
    return (hrs <= 24 ? 
      Number(hrs).toFixed(2) + 'h' : 
      Math.floor(hrs/24) + 'd ' + Number(hrs % 24).toFixed(0) + 'h')
}

const reverse = function (str){
    return str.split("").reduce((rev, char)=> char + rev, ''); 
}
const hex2bin = function (hex){
    return ("00000000" + (parseInt(hex, 16)).toString(2)).substr(-8);
}

const mathdiv = function (A, B) {
    return (A - A%B)/B}

