const getLastBarbyPairTF = function (tweb) {
    tweb.contract(MoonRaceABI, MoonRaceAddress).getLastBarbyPairTF(/* store.state.keyPairTimeFrameListSelectedcomplex */2).call((err, LastBar) => {
        if (err)
            return console.error('getLastBarbyPairTF something wrong ', err);
        console.log('LastBar', LastBar, 'from hex to integer', parseInt(LastBar._hex, 16), 'store.state.betKey=', store.state.betKey)
        store.dispatch('setbetKey', LastBar/* parseInt(LastBar._hex, 16) */)
        tweb.contract(MoonRaceABI, MoonRaceAddress).GetBetState(store.state.betKey).call((err2, betState) => {
            if (err2)
                return console.error('GetBetState something wrong', err2);
            console.log('betState', betState);
            store.dispatch('setBetState', betState)
        })
    })
}

