<script>
import { mapState } from 'vuex';
import { MoonRaceAddress, MoonRaceABI } from '../util/constants/MoonRace.js'
import { GetBallance } from '../util/getTronWeb.js'
export default {
  data () {
    return {
      outline: [[false, false, false], [false, false, false], [false, false, false], [false, false, false], [false, false, false]],
      gameKindDescription: 'Make your prediction!',
      //gameKind: '',
      gameKindID: [false, false, false, false, false],
      image_dots: [-1, -1, -1, -1],
      gameAwardParts: [[false, "", 0],   [false, "", 0],   [false, "", 0],   [false, "", 0]]
    }
  },  
  name: 'mainpage',
  props: {
    msg: String
  },
  computed:
  mapState({
    safe_state: 'safeState',
    User_name: 'mainAddress',
    user_balance_trx: state => Math.trunc(state.trxBallanceNice).toLocaleString(),
    isConnected: state => { console.log('tronWeb.connected', state.tronWeb.connected); return state.tronWeb.connected },
    gameKind: 'gameKind',
    gameKindName: 'gameKindName',
    gameStakeNice: 'gameStakeNice',
    gameStakeReal: 'gameStakeReal',
    GameKindList: 'GameKindList',
    betKey: 'betKey',
    betKeyStakeClosed: 'betKeyStakeClosed',
    tronWeb: 'tronWeb',
    stakesHistory: 'stakesHistory',
    historyType: 'historyType',
    stakesHistoryFiltered: state => {
      let res = []
      if (state.historyType == '') {
        return state.stakesHistory
      }
      if (state.historyType == 'my') {
        state.stakesHistory.forEach(element => {
          if (element.address == state.mainAddress) { 
            res.push(element) 
          }
        })
      }
      return res
    },
    /*
    TECmM....XnRdn
    */
    passedTimes: state => {
      let res = []
      state.AllTicksList.forEach(element => {
        res.push(element.time)
      })
      return res
    },
    futureTimes: state => {
      let res = []
      state.FutureTimeList.forEach(element => {
        res.push(element)
      })
      return res
    },
    image_smoke: state => {
      let res = []
      //console.log('state.AllTicksList1 = ', state.AllTicksList);
      if (state.AllTicksList.length >= 5) {
        for (let index = 1; index < 5; index++) {
          if (index == 1) {
            console.log('state.AllTicksList4 = ', state.AllTicksList[index].b6);
            res.push(state.AllTicksList[index].b6 == 1 ? 3 : 1)
            console.log('-state.AllTicksList4 = ', state.AllTicksList[index].b6);
          } else {
            console.log('state.AllTicksList else = ', state.AllTicksList[index].b6);
            res.push(
              state.AllTicksList[index-1].b6 == 1 ? 
                (state.AllTicksList[index].b6 == 1 ? 3 : 1) :
                (state.AllTicksList[index].b6 == 1 ? 0 : 2))
            console.log('- state.AllTicksList else = ', state.AllTicksList[index].b6);
          }
        }
      } else {
        res.push([1,1,1,1,1])
      }
      return res
    }
  }),
  beforeCreate () {    
    console.log('started mainpage.vue this.$store.dispatch("registerTronWeb")')
    this.$store.dispatch('registerTronWeb')
  },
  methods: {
    BitStrToHex(bitwiseSTR) {
      return '0x'+parseInt(bitwiseSTR, 2).toString(16).toUpperCase();
    },
    histBntClick(val) {
      this.$store.dispatch('updateHistoryType', val)
    },
    BoostBtnClick() {
      if (!this.betKeyStakeClosed & this.gameKind > 0) {
        let binaryStakes = this.outline.map(item => {return item[2] ? (item[0] ? 0 : 1) :0}).reduce((binSTR, elem) => {return elem.toString() + binSTR})
        let hexStakes = this.BitStrToHex(binaryStakes)
        let ValueSafe = 0
        let hexSafe = this.BitStrToHex('000')
        //console.log('Boost! testBar', 'binaryStakes', binaryStakes, 'hexStakes=', hexStakes)
        console.log('Boost!', this.betKey, this.gameKind, hexStakes, this.User_name, this.gameStakeReal, 'bin', binaryStakes, 'ValueSafe=', ValueSafe, 'hexSafe=', hexSafe, ' this.tronWeb=', this.tronWeb, ' window.tronWeb=', window.tronWeb)
        try {
          //console.log(this.tronWeb.instance, window.tronWeb)
          // function makeDirectStakeTRX (uint64 betKey, uint8 gameKind, bytes1 prediction, bytes1 _bSafe, uint256 _uSafeValue) public payable {
          // let contract = await tronWeb.contract({abi});
          //let _contract = window.tronWeb.contract({abi: MoonRaceABI, address: MoonRaceAddress})
          //console.log('_contract', _contract)
          //_contract.makeDirectStakeTRX(this.betKey, this.gameKind, hexStakes, hexSafe, ValueSafe).send({from: this.User_name, callValue: this.gameStakeReal})
          this.tronWeb.instance.contract(MoonRaceABI, MoonRaceAddress).makeDirectStakeTRX(this.betKey, this.gameKind, hexStakes, hexSafe, ValueSafe).send({from: this.User_name, callValue: this.gameStakeReal})
          // 02:35 02:40UP 02:45DOWN
          console.log('Boost sended GetBallance() ')
          GetBallance();
        } catch (err) {
          console.log('Error BoostBtnClick', err);
        }
      }
    },
    changeStakeBtn(stakeValNice) {
      this.$store.dispatch('setGameStake', stakeValNice)
      this.gameAwardParts = this.calcGameAwardParts(this.gameKind)
    },
    calcGameAwardParts(gKind) {
      return (
        (gKind == 1 ? 
            [[true, "2/2", this.gameStakeNice * 2], [false, "", 1], [false, "", 0], [false, "", 0]] : 
          (gKind == 2 ? 
            [[true, "2/3", this.gameStakeNice / 2], [true, "3/3", this.gameStakeNice * 4], [false, "", 0], [false, "", 0]] : 
            (gKind == 3 ? 
              [[true, "2/4", this.gameStakeNice / 2], [true, "3/4", this.gameStakeNice * 2], [true, "4/4", this.gameStakeNice * 8], [false, "", 0]] : 
              (gKind == 4 ? 
                [[true, "2/5", this.gameStakeNice / 2], [true, "3/5", this.gameStakeNice * 2], [true, "4/5", this.gameStakeNice * 4], [true, "5/5", this.gameStakeNice * 16]] : 
                [[false, "", 0],   [false, "", 0],   [false, "", 0],   [false, "", 0]]
              )
            )
          )
        )
      )
    },
    drawDots(gKind) {
      for (let i = 4; i > 0; i--) {
        // if previouse is missed, just set it upper
        if (i <= gKind) {
          if (!this.outline[i-1][2]) {
            this.$set(this.outline[i-1], [0], true)
            this.$set(this.outline[i-1], [2], true)
          }
        }
        if (this.outline[i-1][0] && this.outline[i][0]) /*up-up*/ {
          this.$set(this.image_dots, i-1, 2)
          console.log('up-up')
        }
        if (this.outline[i-1][0] && this.outline[i][1]) /*up-down*/ {
          this.$set(this.image_dots, i-1, 0)
          console.log('up-down')
        }
        if (this.outline[i-1][1] && this.outline[i][1]) /*down-down*/ {
          this.$set(this.image_dots, i-1, 3)
          console.log('down-down')
        }
        if (this.outline[i-1][1] && this.outline[i][0]) /*down-up*/ {
          this.$set(this.image_dots, i-1, 1)
          console.log('down-up')
        }
      }
      if ( gKind > this.gameKind || gKind == -1 ) {
        this.$store.dispatch('SetGameKind', [
          gKind, this.GameKindList[0][gKind]])
        this.$store.dispatch('setSafeState', 
          gKind == 0 ? [0,0,0] : (gKind == 1 ? [0,0,0] : (gKind == 2 ? [1,0,0] : (gKind == 3 ? [1,1,0] : (gKind == 4 ? [1,1,1] : [0,0,0])))))

        this.gameAwardParts = this.calcGameAwardParts(gKind)
          
      }
      console.log('outline', this.outline, 'image_dots', this.image_dots, 'gKind is set on', gKind, ' this.gameAwardParts', this.gameAwardParts)
    },
    resetBntClick() {
      this.outline = [[false, false, false], [false, false, false], [false, false, false], [false, false, false], [false, false, false]]
      this.image_dots = [-1, -1, -1, -1]
      this.drawDots(-1)      
      this.$store.dispatch('SetGameKind', [-1, ''])
      this.$store.dispatch('setSafeState', [0,0,0])
      this.gameAwardParts = [[false, "", 0],   [false, "", 0],   [false, "", 0],   [false, "", 0]]
    },
    safeBntClick(btnN) {      
      if (this.safe_state[btnN] == 2) {
        let res = []
        this.safe_state.forEach(element => { res.push(element) });
        res[btnN] = 1
        console.log('btnN', btnN, 'res case 2', res)
        this.$store.dispatch('setSafeState', res)
      } 
      else if (this.safe_state[btnN] == 1) {
        let res = []
        for (let index = 0; index < 3; index++) {
          res.push(btnN == index ? 2 : (this.safe_state[index] == 2 ? 1 : this.safe_state[index] ))
        }
        console.log('btnN', btnN, 'res case 1', res)
        this.$store.dispatch('setSafeState', res)
      }
      //= this.gameKind = 0 ? [0,0,0] : (this.gameKind = 1 ? [0,0,0] : (this.gameKind = 2 ? [1,0,0] : (this.gameKind = 3 ? [1,1,0] : (this.gameKind = 4 ? [1,1,1] : [0,0,0]))))      
    },
    predictionBntClick(event) {
      if (event == 0) {
        this.$set(this.outline[0], [0], true)
        this.$set(this.outline[0], [1], false)
        this.$set(this.outline[0], [2], true)
        this.gameKindDescription = event
        this.drawDots(0)
      } else if (event == 1) {
        this.$set(this.outline[0], [0], false)
        this.$set(this.outline[0], [1], true)
        this.$set(this.outline[0], [2], true)
        this.gameKindDescription = event
        this.drawDots(0)
      } else if (event == 10) {
        this.$set(this.outline[1], [0], true)
        this.$set(this.outline[1], [1], false)
        this.$set(this.outline[1], [2], true)
        this.drawDots(1)
      } else if (event == 11) {
        this.$set(this.outline[1], [0], false)
        this.$set(this.outline[1], [1], true)
        this.$set(this.outline[1], [2], true)
        this.drawDots(1)
      } else if (event == 20) {
        this.$set(this.outline[2], [0], true)
        this.$set(this.outline[2], [1], false)
        this.$set(this.outline[2], [2], true)
        this.drawDots(2)
      } else if (event == 21) {
        this.$set(this.outline[2], [0], false)
        this.$set(this.outline[2], [1], true)
        this.$set(this.outline[2], [2], true)
        this.drawDots(2)
      } else if (event == 30) {
        this.$set(this.outline[3], [0], true)
        this.$set(this.outline[3], [1], false)
        this.$set(this.outline[3], [2], true)
        this.drawDots(3)
      } else if (event == 31) {
        this.$set(this.outline[3], [0], false)
        this.$set(this.outline[3], [1], true)
        this.$set(this.outline[3], [2], true)
        this.drawDots(3)
      } else if (event == 40) {
        this.$set(this.outline[4], [0], true)
        this.$set(this.outline[4], [1], false)
        this.$set(this.outline[4], [2], true)
        this.drawDots(4)
      } else if (event == 41) {
        this.$set(this.outline[4], [0], false)
        this.$set(this.outline[4], [1], true)
        this.$set(this.outline[4], [2], true)
        this.drawDots(4)
      }       
      console.log("this.outline", this.outline)
    }    
  }
  
}
</script>